/**
 * Las mutaciones de Vuex para el mdulo de categoras.
 */
export default{
  /**
   * Actualiza las categoras del estado.
   * @param {Object} state - El estado de Vuex del mdulo de categoras.
   * @param {Object[]} categories - Las categoras con las que actualizar el estado.
   */
  setCategories(state, categories) {
    state.categories = categories;
  },

  /**
   * Actualiza el nombre de una categora del estado.
   * @param {Object} state - El estado de Vuex del mdulo de categoras.
   * @param {Object} payload - El payload con la informacin que se pasar a la mutacin.
   * @param {string} payload.categoryId - El id de la categora a actualizar.
   * @param {string} payload.categoryName - El nombre de la categora a actualizar.
   * @returns {Promise} - Devuelve una promesa que se resuelve cuando la solicitud se completa.
   */
  updateCategory(state, {categoryId, categoryName}) {
    const index = state.categories.findIndex(c => c.categoryId === categoryId);
    state.categories[index].categoryName = categoryName;
  },

  /**
   * Agrega una nueva categora al estado.
   * @param {Object} state - El estado de Vuex del mdulo de categoras.
   * @param {Object} payload - El payload con la informacin que se pasar a la mutacin.
   * @param {string} payload.categoryName - El nombre de la categora a agregar.
   * @returns {Promise} - Devuelve una promesa que se resuelve cuando la solicitud se completa.
   */
  addNewCategory(state, { categoryName }) {
    state.categories.push({
      categoryName,
    });
  },

  /**
   * Elimina una categora del estado.
   * @param {Object} state - El estado de Vuex del mdulo de categoras.
   * @param {string} categoryId - El id de la categora a eliminar.
   * @returns {Promise} - Devuelve una promesa que se resuelve cuando la solicitud se completa.
   */
  deleteCategory(state, categoryId) {
    state.categories = state.categories.filter(
      c => c.categoryId !== categoryId
    );
  }
}