/**
 * Define los getters del mdulo de categoras.
 */
export default {
  /**
   * Devuelve las categoras del estado.
   * @param {Object} state - El estado de Vuex del mdulo de categoras.
   * @returns {Object[]} - Las categoras del estado.
   */
  categories: (state) => state.categories,

  /**
   * Devuelve un valor booleano que indica si el usuario tiene categoras o no.
   * @param {Object} state - El estado de Vuex del mdulo de categoras.
   * @returns {boolean} - Un valor booleano que indica si el usuario tiene categoras o no.
   */
  hasCategories: (state) => state.categories.length > 0,
}
