/**
 * Mutaciones del mdulo de autenticacin.
 */
export default{
  /**
   * Devuelve el usuario del estado.
   * @param {Object} state - El estado actual de Vuex del mdulo de autenticacin.
   * @param {Object} payload - El payload con la informacin que se pasar a la mutacin.
   */
  setUser(state, payload){
    state.token = payload.token;
    state.user = payload.user;
    state.didAutoLogout = false;
  },

  /**
   * Establece el valor de didAutoLogout en true.
   * @param {Object} state - El estado actual de Vuex del mdulo de autenticacin.
   */
  setAutoLogout(state){
   state.didAutoLogout = true;
  }
};