/**
 * Las mutaciones de Vuex para el mdulo de imgenes.
 */
export default {
  /**
   * Establece las imgenes del estado.
   * @param {Object} state - El estado de Vuex del mdulo de imgenes.
   * @param {Object[]} pictures - Las imgenes a establecer.
   */
  setPictures(state, pictures) {
    state.pictures = Array.isArray(pictures) ? pictures : [];
  },

  /**
   * Elimina una imagen del estado.
   * @param {Object} state - El estado de Vuex del mdulo de imgenes.
   * @param {string} pictureId - El ID de la imagen a eliminar.
   */
  deletePicture(state, pictureId) {
    state.pictures = state.pictures.filter(
      p => p.id !== pictureId
    );
  }
}