/**
 * Define los getters del mdulo de imgenes.
 */
export default {
  /**
   * Devuelve las imgenes del estado.
   * @param {Object} state - El estado de Vuex del mdulo de imgenes.
   * @returns {Object[]} - Las imgenes del estado.
   */
  pictures: (state) => state.pictures,

  /**
   * Devuelve un valor booleano que indica si el usuario tiene imgenes o no.
   * @param {Object} state - El estado de Vuex del mdulo de imgenes.
   * @returns {boolean} - Un valor booleano que indica si el usuario tiene imgenes o no.
   */
  hasPictures: (state) => state.pictures.length > 0,
};